/* @import '~antd/dist/antd.css'; */

*{
  padding: 0%;
  margin: 0%;
}
a{
  text-decoration: none;
}

li {
  list-style: none;
}
body{
  background-color: rgb(226, 224, 224);
}
.header{
  height: 50px;
  background-color: #444;
  width: 100%;
}
.header header{
  width: 1200px;
  margin: 0 auto;
  line-height: 50px;
}
.header header a{
  margin: 0 20px;
  color: #ccc;
  cursor: pointer;
}
.header img{
  width: 120px;
}

/*content*/
.content{
  width: 1200px;
  margin: 30px auto 0;
}
.left {
  width: 900px;
  float: left;
  background-color: white;
}
.right {
  width:270px;
  float: right;
  height: 500px;

}

.nav a{
  margin: 0 20px;
  font-size: 16px;
  
}




/*right*/
.span-info {
  display: inline-block;
  float: none;
  padding: 3px 10px;
  border: none;
  margin: 0;
  font-size: 14px;
  transition: all .2s ease-in-out;
  cursor: pointer;
  letter-spacing: 2px;
  box-shadow: none;
  border-radius: 3px;
  line-height: 2em;
  vertical-align: middle;
  color: #fff;
  background-color: #5bc0de;
}

.panel{
  font-size: 13px;
  margin-bottom: 13px;
  display: block;
  margin-top: 10px;
}

.inner{
  padding: 10px;
  border-radius: 0 0 3px 3px;
  background-color: #fff;
  line-height: 2em;
  font-size: 13px;
}
.cell{
  padding-right: 10px;
  background: #fff;
  border-top: 1px solid #f0f0f0;
  position: relative;
  padding: 10px 0 10px 10px;
  font-size: 14px;
  overflow: hidden;
}


/* bottom */
.footer_main{
  text-align: center;
  margin: 0 auto;
  color: #807c7c;
  padding: 20px 0;
  font-size: 13px;
  line-height: 2em;
  background: white;
  margin-top: 600px;
}



.num li{
  float: left;
  width: 33px;
  height: 25px;
  border: 0.5px solid #7c7979;
  text-align: center;
  padding-top: 5px;
  color: #7c7979;
  cursor: pointer;
  text-align: center;
  margin-left: 15px;
}








